<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  import { getCurrentUserInfo } from "../utils.js"
  const dispatch = new createEventDispatcher()

  export let learningMaterial
  let teachers = []
  let subjects = []
  let id
  let teacher
  let subject
  let title
  let description
  let files = []
  const currentUserInfo = getCurrentUserInfo()

  onMount(async () => {
    teachers = await fetch("/api/v1/teacher", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
    subjects = await fetch("/api/v1/subject", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
    id = learningMaterial.id
    teacher = learningMaterial.teacher
    subject = learningMaterial.subject
    title = learningMaterial.title
    description = learningMaterial.description
  })

  async function uploadLearningMaterial() {
    const formData = new FormData()
    formData.append("id", id)
    formData.append("title", title)
    formData.append("description", description)
    formData.append("subjectId", subject.id)
    formData.append("teacherId", teacher.id)
    formData.append("file", files[0])
    await fetch("/api/v1/learningMaterial/currentUser/uploadFile", {
      headers: {
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "PUT",
      body: formData,
    }).then(async () => await dispatch("refreshLearningMaterials"))
  }
</script>

<label>Title: <input type="text" bind:value={title} /></label>
<label>Description: <input type="text" bind:value={description} /></label>
{#if currentUserInfo.user?.role == "Admin"}
  <label>
    Teacher:
    <select bind:value={teacher}>
      <option value="">Select a Teacher</option>
      {#each teachers as teacher}
        <option value={teacher}>{teacher.userInfo.user.name}</option>
      {/each}
    </select>
  </label>
{/if}
<label>
  Subject:
  <select bind:value={subject}>
    <option value="">Select a Subject</option>
    {#each subjects as subject}
      <option value={subject}>{subject.name}</option>
    {/each}
  </select>
</label>
<label>File: <input type="file" name="file" bind:files /></label>
<button on:click={async () => await uploadLearningMaterial()}>Submit</button>
