<script>
  import { createEventDispatcher, onMount } from "svelte"
  import AddSection from "./Section/AddSection.svelte"
  import EditSection from "./Section/EditSection.svelte"
  import SectionTable from "./Section/SectionTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  let sectionTable
  let addSectionModal
  let editSectionModal
  let deleteSectionModal
  let selectedSection

  async function onRefreshSections() {
    await sectionTable.refreshSections()
    await addSectionModal.close()
    await editSectionModal.close()
    await deleteSectionModal.close()
  }

  async function onEditSection(e) {
    selectedSection = e.detail
    await editSectionModal.toggle()
  }

  async function onDeleteSection(e) {
    selectedSection = e.detail
    await deleteSectionModal.toggle()
  }

  async function deleteSection() {
    await fetch(`/api/v1/section/${selectedSection.id}`, {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "DELETE",
    }).then(async () => await onRefreshSections())
  }
</script>

<Container>
  <button on:click={async () => await addSectionModal.toggle()}
    >New Section</button
  >
  <Modal bind:this={addSectionModal}>
    <AddSection on:refreshSections={onRefreshSections} />
  </Modal>
  <br />
  <SectionTable
    bind:this={sectionTable}
    on:editSection={onEditSection}
    on:deleteSection={onDeleteSection}
    canSelect={false}
    canEdit={true}
    canDelete={true}
  />
  <Modal bind:this={editSectionModal}>
    <EditSection
      section={selectedSection}
      on:refreshSections={onRefreshSections}
    />
  </Modal>
  <Modal bind:this={deleteSectionModal}>
    Are you Sure you want to delete section: "{selectedSection.name}"?
    <button on:click={async () => await deleteSection()}>Delete</button>
  </Modal>
</Container>
