<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()

  export let teacher
  let username
  let password
  let teacherName
  let role
  let status

  onMount(async () => {
    username = teacher.userInfo.user.username
    teacherName = teacher.userInfo.user.name
    role = teacher.userInfo.user.role
    status = teacher.userInfo.user.status
  })

  async function updateTeachers() {
    await fetch("/api/v1/teacher", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "PUT",
      body: JSON.stringify({
        id: teacher.id,
        user: {
          username,
          password,
          name: teacherName,
          role,
          status,
        },
      }),
    }).then(async () => await dispatch("refreshTeachers"))
  }
</script>

<label>Username: <input type="text" bind:value={username} /></label>
<label>Password: <input type="password" bind:value={password} /></label>
<label>Name: <input type="text" bind:value={teacherName} /></label>
<label>Status: <input type="text" bind:value={status} /></label>
<button on:click={async () => await updateTeachers()}>Submit</button>
