<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  export let canSelect = true
  export let canEdit
  export let canDelete
  let anouncements = []

  onMount(async () => {
    await refreshAnouncements()
  })

  export async function refreshAnouncements() {
    anouncements = await fetch("/api/v1/anouncement", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
  }

  const months = {
    0: "January",
    1: "February",
    2: "March",
    3: "April",
    4: "May",
    5: "June",
    6: "July",
    7: "August",
    8: "September",
    9: "October",
    10: "November",
    11: "December",
  }

  const formatDate = (d) => {
    d = new Date(d)
    return `${
      months[d.getMonth()]
    } ${d.getDate()}, ${d.getFullYear()} ${d.toLocaleTimeString()}`
  }
</script>

{#if anouncements.length > 0}
  <table>
    <thead>
      <tr>
        <td>Title</td>
        <td>Description</td>
        <td>Date created</td>
        <td>Action</td>
      </tr>
    </thead>
    <tbody>
      {#each anouncements as anouncement}
        <tr>
          <td>{anouncement.title}</td>
          <td>{anouncement.description}</td>
          <td>{formatDate(anouncement.dateCreated)}</td>
          <td>
            {#if canSelect}
              <button
                on:click={async () =>
                  await dispatch("selectAnouncement", anouncement)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button
                on:click={async () =>
                  await dispatch("editAnouncement", anouncement)}>Edit</button
              >
            {/if}
            {#if canDelete}
              <button
                on:click={async () =>
                  await dispatch("deleteAnouncement", anouncement)}
                >Delete</button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
