<script>
  import { fade } from "svelte/transition"
  let isOpen = false

  export async function toggle() {
    isOpen = !isOpen
  }

  export async function close() {
    isOpen = false
  }
</script>

{#if isOpen}
  <div class="modal" transition:fade on:click|self={async () => await close()}>
    <div class="modal-content">
      <slot />
    </div>
  </div>
{/if}

<style>
  .modal {
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
  }

  .modal-content {
    background-color: var(--background-body);
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    margin-bottom: 200px;
  }
</style>
