<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  export let canSelect = true
  export let canEdit
  export let canDelete
  let teachers = []

  onMount(async () => {
    await refreshTeachers()
  })

  export async function refreshTeachers() {
    teachers = await fetch("/api/v1/teacher", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
  }
</script>

{#if teachers.length > 0}
  <table>
    <thead>
      <tr>
        <td>Username</td>
        <td>Name</td>
        <td>Role</td>
        <td>Status</td>
        <td>Action</td>
      </tr>
    </thead>
    <tbody>
      {#each teachers as teacher}
        <tr>
          <td>{teacher.userInfo.user.username}</td>
          <td>{teacher.userInfo.user.name}</td>
          <td>{teacher.userInfo.user.role}</td>
          <td>{teacher.userInfo.user.status}</td>
          <td>
            {#if canSelect}
              <button
                on:click={async () => await dispatch("selectTeacher", teacher)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button
                on:click={async () => await dispatch("editTeacher", teacher)}
                >Edit</button
              >
            {/if}
            {#if canDelete}
              <button
                on:click={async () => await dispatch("deleteTeacher", teacher)}
                >Delete</button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
