<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  import jwt_decode from "jwt-decode"
  const dispatch = new createEventDispatcher()

  let selectedItem
  let teachers = []
  let subjects = []
  let teacher
  let subject
  let title
  let timeLimit
  let dateOfSubmission
  let items = []
  let itemNumber
  let type
  let question
  let answer
  let incorrectAnswers = []
  let incorrectAnswerInput
  let isTeacher

  onMount(async () => {
    const token = Cookies.get("token")
    teachers = await fetch("/api/v1/teacher", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
    subjects = await fetch("/api/v1/subject", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())

    if (token != undefined) {
      let decoded = jwt_decode(token)
      let userInfo = decoded?.userInfo

      if (userInfo?.user?.role == "Teacher") {
        teacher = teachers.filter(
          (t) => t.userInfo.user.id == userInfo.user.id
        )[0]
        isTeacher = true
      }
    }
  })

  async function newQuiz() {
    await fetch("/api/v1/quiz", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "POST",
      body: JSON.stringify({
        title,
        timeLimit,
        dateOfSubmission: new Date(dateOfSubmission).toISOString(),
        teacher: {
          id: teacher.id,
        },
        subject: {
          id: subject.id,
        },
        items,
      }),
    }).then(async () => await dispatch("refreshQuizzes"))
  }

  async function deleteIncorrectAnswer(incorrectAnswer) {
    incorrectAnswers = incorrectAnswers.filter((m) => m != incorrectAnswer)
  }

  async function addIncorrectAnswer() {
    incorrectAnswers.push(incorrectAnswerInput)
    let tmp = incorrectAnswers
    incorrectAnswers = []
    incorrectAnswers = tmp
    incorrectAnswerInput = ""
  }

  async function addItem() {
    items.push({
      question,
      answer,
      type,
      incorrectAnswers,
    })
    let tmp = items
    question = ""
    answer = ""
    incorrectAnswers = []
    items = []
    items = tmp
  }

  async function updateItem() {
    items[itemNumber - 1] = {
      question,
      answer,
      type,
      incorrectAnswers,
    }
    let tmp = items
    question = ""
    answer = ""
    type = ""
    incorrectAnswers = []
    items = []
    items = tmp
  }

  async function editItem(item, number) {
    itemNumber = number + 1
    selectedItem = item
    question = item.question
    answer = item.answer
    type = item.type
    incorrectAnswers = item.incorrectAnswers
  }

  async function deleteItem(item) {
    items = items.filter((i) => i != item)
  }
</script>

<label>Title: <input type="text" bind:value={title} /></label>
{#if !isTeacher}
  <label>
    Teacher:
    <select bind:value={teacher}>
      <option value="">Select a Teacher</option>
      {#each teachers as teacher}
        <option value={teacher}>{teacher.userInfo.user.name}</option>
      {/each}
    </select>
  </label>
{/if}
<label>
  Subject:
  <select bind:value={subject}>
    <option value="">Select a Subject</option>
    {#each subjects as subject}
      <option value={subject}>{subject.name}</option>
    {/each}
  </select>
</label>
<label
  >Time limit (seconds): <input type="number" bind:value={timeLimit} /></label
>
<label
  >Date of Submission: <input
    type="datetime-local"
    bind:value={dateOfSubmission}
  /></label
>
<br />
<label>No.: <input readonly type="number" bind:value={itemNumber} /></label>
<label>
  Type:
  <select bind:value={type}>
    <option value="Multiple choices">Multiple choices</option>
    <option value="True or False">True or False</option>
    <option value="Matching type">Matching type</option>
    <option value="Fill in the blank">Fill in the blank</option>
  </select>
</label>
<label>Question: <input bind:value={question} /></label>
{#if type === "True or False"}
  <div style="display: flex; flex-wrap: wrap;">
    <label><input bind:group={answer} type="radio" value="true" />True</label>
    <label><input bind:group={answer} type="radio" value="false" />False</label>
  </div>
{:else}
  <label>Answer: <input bind:value={answer} /></label>
{/if}
{#if type === "Multiple choices"}
  <label>
    Incorrect answer:
    <input bind:value={incorrectAnswerInput} />
    <button on:click={async () => await addIncorrectAnswer()}
      >Add incorrect answer</button
    >
  </label>
  <br />
  {#if incorrectAnswers.length > 0}
    <table>
      <thead>
        <tr>
          <th>Answer</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {#each incorrectAnswers as incorrectAnswer}
          <tr>
            <td>{incorrectAnswer}</td>
            <td
              ><button
                on:click={async () =>
                  await deleteIncorrectAnswer(incorrectAnswer)}>Delete</button
              ></td
            >
          </tr>
        {/each}
      </tbody>
    </table>
  {/if}
{/if}
<button on:click={async () => await addItem()}>Add Item</button>
<button on:click={async () => await updateItem()}>Update Item</button>
<br />
{#if items.length > 0}
  <table>
    <thead>
      <tr>
        <th>No. </th>
        <th>Type</th>
        <th>Question</th>
        <th>Answer</th>
        <th>Incorrect answers</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each items as item, i}
        <tr>
          <td>{i + 1} </td>
          <td>{item.type}</td>
          <td>{item.question}</td>
          <td>{item.answer}</td>
          <td
            >{item.type === "Multiple choices" ? item.incorrectAnswers : ""}</td
          >
          <td
            ><button on:click={async () => await editItem(item, i)}>Edit</button
            ><button on:click={async () => await deleteItem(item)}
              >Delete</button
            ></td
          >
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
<br />
<button on:click={async () => await newQuiz()}>Submit</button>
