<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  import jwt_decode from "jwt-decode"
  import Container from "../shared/Container.svelte"
  import Modal from "../shared/Modal.svelte"
  export let quiz
  const dispatch = new createEventDispatcher()
  let students = []
  let student
  let answers = []
  let multipleChoices = []
  let timeoutModal
  let counter
  let isStudent

  onMount(async () => {
    const token = Cookies.get("token")
    let i = 0
    quiz.items.forEach((item) => {
      if (item.type === "Multiple choices") {
        multipleChoices[i] = [...item.incorrectAnswers, item.answer]
        multipleChoices[i].sort(() => Math.random() - 0.5)
        i++
      }
    })
    const tmp = quiz
    quiz = undefined
    quiz = tmp
    students = await fetch("/api/v1/student", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())

    if (quiz?.timeLimit != 0) {
      counter = quiz?.timeLimit
      let counterInterval = setInterval(() => {
        counter--
      }, 1000)

      setTimeout(async () => {
        clearInterval(counterInterval)
        counter = 0
        await checkQuiz()
      }, quiz?.timeLimit * 1000)
    }

    if (token != undefined) {
      let decoded = jwt_decode(token)
      let userInfo = decoded?.userInfo

      if (userInfo?.user?.role == "Student") {
        alert(JSON.stringify(students))
        student = students.filter(
          (s) => s.userInfo.user.id == userInfo.user.id
        )[0]
        isStudent = true
      }
    }
  })

  async function checkQuiz() {
    await fetch("/api/v1/quizResult/check", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "POST",
      body: JSON.stringify({
        quiz: {
          id: quiz.id,
          title: quiz.title,
        },
        student: {
          id: student.id,
          user: {
            name: student.userInfo.user.name,
          },
          guardian: {
            contactNumber: student.guardian.contactNumber,
          },
        },
        answers,
      }),
    }).then(async () => {
      await dispatch("refreshQuizResults")
      await timeoutModal.toggle()
      setTimeout(() => {
        document.location.hash = "quizResults"
        document.location.reload()
      }, 1000)
    })
  }
</script>

<Container>
  {#if counter != 0}
    Timer: {counter} seconds left
  {/if}
  {quiz.title} ({quiz.subject.name}) by {quiz.teacher.userInfo.user.name}<br />
  {#if !isStudent}
    Student:
    <select bind:value={student}>
      <option value="">Select a Student</option>
      {#each students as student}
        <option value={student}>{student.userInfo.user.name}</option>
      {/each}
    </select>
    <br />
  {/if}
  {#each quiz.items as item, i}
    {i + 1}.&nbsp;{item.question}<br />
    {#if item.type === "Multiple choices" && multipleChoices[i]?.length > 0}
      <div style="display: flex; flex-wrap: wrap;">
        {#each multipleChoices[i] as answer}
          &nbsp;&nbsp;<label>
            <input
              bind:group={answers[i]}
              type="radio"
              value={answer}
            />{answer}</label
          >
        {/each}
      </div>
    {:else if item.type === "True or False"}
      <div style="display: flex; flex-wrap: wrap;">
        &nbsp;&nbsp;<label>
          <input bind:group={answers[i]} type="radio" value="true" />True</label
        >
        &nbsp;&nbsp;<label>
          <input
            bind:group={answers[i]}
            type="radio"
            value="false"
          />False</label
        >
      </div>
    {:else if item.type === "Fill in the blank"}
      &nbsp;&nbsp;Answer:<input id={`${i}Input`} bind:value={answers[i]} />
    {/if}
  {/each}
  <br />
  <button on:click={async () => await checkQuiz()}>Submit</button>
  <Modal bind:this={timeoutModal}>Time's up. Submitting answers...</Modal>
</Container>
