<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()

  export let student
  let sections = []
  let section
  let username
  let password
  let studentName
  let role
  let status
  let guardianName
  let contactNumber

  onMount(async () => {
    sections = await fetch("/api/v1/section", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
    username = student.userInfo.user.username
    studentName = student.userInfo.user.name
    role = student.userInfo.user.role
    status = student.userInfo.user.status
    section = student.section
    guardianName = student.guardian.name
    contactNumber = student.guardian.contactNumber
  })

  async function updateStudents() {
    await fetch("/api/v1/student", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "PUT",
      body: JSON.stringify({
        id: student.id,
        userInfo: {
          user: {
            username,
            password,
            name: studentName,
            role,
            status,
          },
        },
        section: {
          id: section.id,
        },
        guardian: {
          name: guardianName,
          contactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshStudents"))
  }
</script>

<label>Username: <input type="text" bind:value={username} /></label>
<label>Password: <input type="password" bind:value={password} /></label>
<label>Name: <input type="text" bind:value={studentName} /></label>
<label>
  Section:
  <select bind:value={section}>
    <option value="">Select a Section</option>
    {#each sections as section}
      <option value={section}>{section.name}</option>
    {/each}
  </select>
</label>
<label>Status: <input type="text" bind:value={status} /></label>
<label>Guardian Name: <input type="text" bind:value={guardianName} /></label>
<label
  >Guardian Contact number: <input
    type="text"
    bind:value={contactNumber}
  /></label
>
<button on:click={async () => await updateStudents()}>Submit</button>
