<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  let quizResults = []
  export let canSelect = true
  export let canEdit
  export let canDelete

  onMount(async () => {
    await refreshQuizResults()
  })

  export async function refreshQuizResults() {
    quizResults = await fetch("/api/v1/quizResult", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
  }
</script>

{#if quizResults.length > 0}
  <table>
    <thead>
      <tr>
        <th>Quiz</th>
        <th>Student</th>
        <th>Percentage</th>
        <th>Comment</th>
        <th>Action</th>
      </tr>
    </thead>
    <tbody>
      {#each quizResults as quizResult}
        <tr>
          <td>{quizResult.quiz.title}</td>
          <td>{quizResult.student.userInfo.user.name}</td>
          <td>{quizResult.percentage}</td>
          <td>{quizResult.comment}</td>
          <td>
            {#if canSelect}
              <button
                on:click={async () =>
                  await dispatch("selectQuizResult", quizResult)}>Select</button
              >
            {/if}
            {#if canEdit}
              <button
                on:click={async () =>
                  await dispatch("editQuizResult", quizResult)}>Edit</button
              >
            {/if}
            {#if canDelete}
              <button
                on:click={async () =>
                  await dispatch("deleteQuizResult", quizResult)}>Delete</button
              >
            {/if}</td
          >
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
