<script>
  import { createEventDispatcher } from "svelte"
  import AddLearningMaterial from "./LearningMaterial/AddLearningMaterial.svelte"
  import EditLearningMaterial from "./LearningMaterial/EditLearningMaterial.svelte"
  import LearningMaterialTable from "./LearningMaterial/LearningMaterialTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  let learningMaterialTable
  let addLearningMaterialModal
  let editLearningMaterialModal
  let deleteLearningMaterialModal
  let selectedLearningMaterial

  async function onRefreshLearningMaterials() {
    await learningMaterialTable.refreshLearningMaterials()
    await addLearningMaterialModal.close()
    await editLearningMaterialModal.close()
    await deleteLearningMaterialModal.close()
  }

  async function onEditLearningMaterial(e) {
    selectedLearningMaterial = e.detail
    await editLearningMaterialModal.toggle()
  }

  async function onDeleteLearningMaterial(e) {
    selectedLearningMaterial = e.detail
    await deleteLearningMaterialModal.toggle()
  }

  async function deleteLearningMaterial() {
    await fetch(
      `/api/v1/learningMaterial/currentUser/${selectedLearningMaterial.id}`,
      {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${Cookies.get("token")}`,
        },
        method: "DELETE",
      }
    ).then(async () => await onRefreshLearningMaterials())
  }
</script>

<Container>
  <button on:click={async () => await addLearningMaterialModal.toggle()}
    >New LearningMaterial</button
  >
  <Modal bind:this={addLearningMaterialModal}>
    <AddLearningMaterial
      on:refreshLearningMaterials={onRefreshLearningMaterials}
    />
  </Modal>
  <br />
  <LearningMaterialTable
    bind:this={learningMaterialTable}
    on:editLearningMaterial={onEditLearningMaterial}
    on:deleteLearningMaterial={onDeleteLearningMaterial}
    canSelect={false}
    canEdit={true}
    canDelete={true}
  />
  <Modal bind:this={editLearningMaterialModal}>
    <EditLearningMaterial
      learningMaterial={selectedLearningMaterial}
      on:refreshLearningMaterials={onRefreshLearningMaterials}
    />
  </Modal>
  <Modal bind:this={deleteLearningMaterialModal}>
    Are you Sure you want to delete learningMaterial: "{selectedLearningMaterial.title}"
    by {selectedLearningMaterial.teacher.userInfo.user.name}?
    <button on:click={async () => await deleteLearningMaterial()}>Delete</button
    >
  </Modal>
</Container>
