{:else}
  <input
    on:keypress={submitFromEnter}
    bind:value={username}
    id="username"
    type="text"
    placeholder="Username"
    required="required"
  />
  <input
    on:keypress={submitFromEnter}
    bind:value={password}
    id="password"
    type="password"
    placeholder="Password"
    required="required"
  />
  <button type="button" on:click={submitCredentials}
    ><i class="fa fa-sign-in-alt" />&nbsp <b>SIGN IN</b>
  </button>
{/if}
