<script>
  import QuizTable from "./Quiz/QuizTable.svelte"
  import TakeQuiz from "./QuizResult/TakeQuiz.svelte"
  import Container from "./shared/Container.svelte"
  let quizTable
  let selectedQuiz

  async function onSelectQuiz(e) {
    selectedQuiz = e.detail
  }
</script>

<Container>
  <QuizTable bind:this={quizTable} on:selectQuiz={onSelectQuiz} />
  <br />
  {#if selectedQuiz != undefined}
    <TakeQuiz quiz={selectedQuiz} on:refreshQuizResults />
  {/if}
</Container>
