<script>
  import { createEventDispatcher, onMount } from "svelte"
  import AddSubject from "./Subject/AddSubject.svelte"
  import EditSubject from "./Subject/EditSubject.svelte"
  import SubjectTable from "./Subject/SubjectTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  let subjectTable
  let addSubjectModal
  let editSubjectModal
  let deleteSubjectModal
  let selectedSubject

  async function onRefreshSubjects() {
    await subjectTable.refreshSubjects()
    await addSubjectModal.close()
    await editSubjectModal.close()
    await deleteSubjectModal.close()
  }

  async function onEditSubject(e) {
    selectedSubject = e.detail
    await editSubjectModal.toggle()
  }

  async function onDeleteSubject(e) {
    selectedSubject = e.detail
    await deleteSubjectModal.toggle()
  }

  async function deleteSubject() {
    await fetch(`/api/v1/subject/${selectedSubject.id}`, {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "DELETE",
    }).then(async () => await onRefreshSubjects())
  }
</script>

<Container>
  <button on:click={async () => await addSubjectModal.toggle()}
    >New Subject</button
  >
  <Modal bind:this={addSubjectModal}>
    <AddSubject on:refreshSubjects={onRefreshSubjects} />
  </Modal>
  <br />
  <SubjectTable
    bind:this={subjectTable}
    on:editSubject={onEditSubject}
    on:deleteSubject={onDeleteSubject}
    canSelect={false}
    canEdit={true}
    canDelete={true}
  />
  <Modal bind:this={editSubjectModal}>
    <EditSubject
      subject={selectedSubject}
      on:refreshSubjects={onRefreshSubjects}
    />
  </Modal>
  <Modal bind:this={deleteSubjectModal}>
    Are you Sure you want to delete subject: "{selectedSubject.name}"?
    <button on:click={async () => await deleteSubject()}>Delete</button>
  </Modal>
</Container>
