<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()
  export let canSelect = true
  export let canEdit
  export let canDelete
  let quizzes = []

  onMount(async () => {
    await refreshQuizzes()
  })

  export async function refreshQuizzes() {
    quizzes = await fetch("/api/v1/quiz", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
    }).then((r) => r.json())
  }

  const months = {
    0: "January",
    1: "February",
    2: "March",
    3: "April",
    4: "May",
    5: "June",
    6: "July",
    7: "August",
    8: "September",
    9: "October",
    10: "November",
    11: "December",
  }

  const formatDate = (d) => {
    d = new Date(d)
    return `${
      months[d.getMonth()]
    } ${d.getDate()}, ${d.getFullYear()} ${d.toLocaleTimeString()}`
  }
</script>

{#if quizzes.length > 0}
  <table>
    <thead>
      <tr>
        <th>Title</th>
        <th>Teacher</th>
        <th>Subject</th>
        <th>Time limit (seconds)</th>
        <th>Deadline</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each quizzes as quiz}
        <tr>
          <td>{quiz.title}</td>
          <td>{quiz.teacher.userInfo.user.name}</td>
          <td>{quiz.subject.name}</td>
          <td>{quiz.timeLimit}</td>
          <td
            >{formatDate(quiz.dateOfSubmission)}
            {new Date(quiz.dateOfSubmission) <= new Date()
              ? "Ended"
              : "Started"}</td
          >
          <td>
            {#if canSelect}
              <button
                disabled={new Date(quiz.dateOfSubmission) <= new Date()}
                on:click={async () => await dispatch("selectQuiz", quiz)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button on:click={async () => await dispatch("editQuiz", quiz)}
                >Edit</button
              >
            {/if}
            {#if canDelete}
              <button on:click={async () => await dispatch("deleteQuiz", quiz)}
                >Delete</button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
