<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  const dispatch = new createEventDispatcher()

  export let anouncement
  let title
  let description

  onMount(async () => {
    title = anouncement.title
    description = anouncement.description
  })

  async function updateAnouncements() {
    await fetch("/api/v1/anouncement", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${Cookies.get("token")}`,
      },
      method: "PUT",
      body: JSON.stringify({
        id: anouncement.id,
        title,
        description,
      }),
    }).then(async () => await dispatch("refreshAnouncements"))
  }

  const months = {
    0: "January",
    1: "February",
    2: "March",
    3: "April",
    4: "May",
    5: "June",
    6: "July",
    7: "August",
    8: "September",
    9: "October",
    10: "November",
    11: "December",
  }

  const formatDate = (d) => {
    d = new Date(d)
    return `${
      months[d.getMonth()]
    } ${d.getDate()}, ${d.getFullYear()} ${d.toLocaleTimeString()}`
  }
</script>

Date created: {formatDate(anouncement.dateCreated)}
<label>Title: <input type="text" bind:value={title} /></label>
<label>Description: <input type="text" bind:value={description} /></label>
<button on:click={async () => await updateAnouncements()}>Submit</button>
